<template>
  <div class="cell-container my-background-color-secondary my-view-shadow p-0">
      <div class="row m-0">
        <div class="col-auto col-md-auto order m-1">
            <i class="icon-member"></i>
        </div>
        <div class="col">
            <div class="row row-cols-1 row-cols-sm-2 row-cols-md h-100">
                <div class="col col-md m-0 align-self-center">
                    {{ info.name }}
                </div>
                <div class="col col-md-auto m-0 align-self-center">
                    {{ info.gender }}
                </div>
                <div class="col col-md m-0 align-self-center">
                    {{ age }}歲
                </div>
                <div class="col col-md-auto m-0 align-self-center">
                    {{ new Date(info.birthday).toLocaleDateString() }}
                </div>
                <div class="col col-md m-0 align-self-center">
                    {{ info.height }}cm / {{ info.weight }}kg
                </div>
                <div class="col col-md-auto m-0 align-self-center">
                    BMI: {{ bmi }}
                </div>
                <div class="col col-md-auto m-0 align-self-center">
                    體指率: {{ info.pbf ?? '---' }}%
                </div>
            </div>
        </div>
        <div class="col-auto trash-block my-accent-color-primary d-flex rounded-end">
            <button class="btn icon-trash" @click="onDelete($event)"></button>
        </div>
      </div>
  </div>
</template>

<script>
export default {
    props: {
        info: Object
    },
    computed: {
        bmi: function() {
            if (!this.info.height && !this.info.weight) { return '---' }
            let original = (this.info.weight) / Math.pow((this.info.height / 100), 2);
            let rounded = Math.round(original * 10) / 10;
            return rounded
        },
        age: function() {
            var ageDifMs = Date.now() - new Date(this.info.birthday);
            var ageDate = new Date(ageDifMs); // miliseconds from epoch
            return Math.abs(ageDate.getUTCFullYear() - 1970);
        }
    },
    methods: {
        onDelete: function(e) {
            console.log('Delete!!');
            this.$emit('deleteMember');
            e.stopPropagation()
        }
    }
}
</script>

