    background-position: bottom;
    background-size: 40px;
}
.icon-trash {
    /* height: 50px; */
    width: 60px;
    background-image: url('~@/assets/icon_trash.png');
    background-repeat: no-repeat;
    background-position: center;
    background-size: 30px;
}
