<template>
    <nav class="navbar navigation">
        <div class="container-fluid">
            <span class="navbar-brand mb-0 h1 my-text">學員健身資料系統</span>
        </div>
    </nav>
    <div class="container align-self-stretch p-2">
        <div class="row">
            <div class="col-auto">
                <button class="btn my-accent-color-primary btn-add-member rounded-circle" @click="onAddMember"></button>
            </div>
            <div class="col my-auto">
                <input class="form-control text-start" type="text" placeholder="請輸入學員名稱或編號" v-model="searchText">
            </div>
            <div class="col-auto my-auto">
                <button class="btn my-accent-color-primary" @click="onRemoveSearchText">清除</button>
            </div>
            <div class="col-auto my-auto">
                <button class="btn my-accent-color-secondary">排序</button>
            </div>
        </div>
        <div class="row mt-2">
            <div v-if="filteredInfos.length > 0">
                <member-cell class="col-12 mb-3" v-for="info in filteredInfos" :key="info.id" v-bind:info="info" @click="onClickCell(info)" @delete-member="deleteMember(info)"/>
            </div>
            <div v-else>
                <h1>無資料</h1>
            </div>
        </div>
    </div>
    <add-member class="" v-show="showAddMember" @store-member="storeMember" @close-view="showAddMember = false"></add-member>
</template>

<script>
import MemberCell from '@/components/MemberCell.vue'
import AddMember from '@/components/AddMember.vue'

export default {
  components: { MemberCell, AddMember },
  data: function() {
      return {
          showAddMember: false,
          searchText: '',
          memberInfos: [{ id: 0, name: '王大明', gender: '男', birthday: 543110400000, height: 172, weight: 79, pbf: 20 },
                        { id: 1, name: '陳小美', gender: '女', birthday: 996624000000, height: 155, weight: 52, pbf: 32 }],
      }
  },
  computed: {
      filteredInfos: function () {
          let infos = [];
          if (this.searchText) {
            for (let index in this.memberInfos) {
                let info = this.memberInfos[index];
                if (info.name.includes(this.searchText) || info.id == this.searchText) {
                    infos.push(info);
                }
            }
           } else {
               infos = this.memberInfos;
           }
           return infos
      }
  },
  methods: {
      onClickCell: function(info) {
          console.log(`Selected info: ${info.name}`);
      },
      onAddMember: function() {
          this.showAddMember = true;
      },
      storeMember: function(info) {
          this.showAddMember = false;
          this.memberInfos.push(info);
      },
      deleteMember: function(info) {
          for (let index in this.memberInfos) {
              if (this.memberInfos[index].id === info.id) {
                  this.memberInfos.splice(index, 1);
              }
          }
      },
      onRemoveSearchText: function() {
          this.searchText = '';
      }
  }
}
</script>

