<template>
  <div class="dimming-view">
      <div class="container">
        <div class="model-view align-items-center row vh-100 d-inline-flex">
            <div class="col my-background-color-primary  ps-4 pe-4 rounded-3">
                <div class="row mb-1">
                    <div class="col-auto">
                        <button class="btn my-btn-close" @click="closeAddMember"></button>
                    </div>
                    <div class="col my-auto">
                        <p class="h5 my-auto">新增學員</p>
                    </div>
                    <div class="col-auto my-auto">
                        <button class="btn my-accent-color-primary" @click="storeMember">新增</button>
                    </div>
                </div>
                <!-- input data -->
                <div class="row mb-2">
                    <div class="col my-auto text-start">
                        <label for="nameInput" class="form-label m-0 h5">姓名</label>
                    </div>
                    <div class="col-8">
                        <input id="nameInput" class="form-control text-start" type="text" v-model="name" v-bind:class="{ 'border': hasSubmit && !name, 'border-danger': hasSubmit && !name }">
                    </div>
                </div>
                <div class="row mb-2">
                    <div class="col my-auto text-start">
                        <label for="birthdayInput" class="form-label m-0 h5">生日</label>
                    </div>
                    <div class="col-8">
                        <input id="birthdayInput" class="form-control text-start" type="date" v-model="birthday" v-bind:class="{ 'border': hasSubmit && !birthday, 'border-danger': hasSubmit && !birthday }">
                    </div>
                </div>
                <div class="row mb-2">
                    <div class="col my-auto text-start">
                        <label for="genderInput" class="form-label m-0 h5">性別</label>
                    </div>
                    <div class="col-8">
                        <select class="form-select text-center" v-model="gender">
                            <option value="男">男</option>
                            <option value="女">女</option>
                        </select>
                    </div>
                </div>
                <div class="row mb-2">
                    <div class="col my-auto text-start">
                        <label for="heightInput" class="form-label m-0 h5">身高(cm)</label>
                    </div>
                    <div class="col-8">
                        <input id="heightInput" class="form-control text-start" type="text" v-model="height" v-bind:class="{ 'border': hasSubmit && !height, 'border-danger': hasSubmit && !height }">
                    </div>
                </div>
                <div class="row mb-2">
                    <div class="col my-auto text-start">
                        <label for="weightInput" class="form-label m-0 h5">體重(kg)</label>
                    </div>
                    <div class="col-8">
                        <input id="weightInput" class="form-control text-start" type="text" v-model="weight" v-bind:class="{ 'border': hasSubmit && !weight, 'border-danger': hasSubmit && !weight }">
                    </div>
                </div>
                <div class="row mb-2">
                    <div class="col my-auto text-start">
                        <label for="careerInput" class="form-label m-0 h5">職業</label>
                    </div>
                    <div class="col-8">
                        <input id="careerInput" class="form-control text-start" type="text" v-model="career">
                    </div>
                </div>
                <div class="row mb-2">
                    <div class="col my-auto text-start">
                        <label for="sportFrequencyInput" class="form-label m-0 h5">運動頻率</label>
                    </div>
                    <div class="col-8">
                        <input id="sportFrequencyInput" class="form-control text-start" type="text" v-model="sportFrequancy">
                    </div>
                </div>
                <div class="row mb-2">
                    <div class="col my-auto text-start">
                        <label for="sportGoalInput" class="form-label m-0 h5">運動目標</label>
                    </div>
                    <div class="col-8">
                        <input id="sportGoalInput" class="form-control text-start" type="text" v-model="sportGoal">
                    </div>
                </div>
            </div>
        </div>
      </div>
  </div>
</template>

<script>
export default {
    data: function() {
        return {
            hasSubmit: false,
            name: '',
            birthday: '',
            gender: '男',
            height: '',
            weight: '',
            career: '',
            sportFrequancy: '',
            sportGoal: ''
        }
    },
    methods: {
        closeAddMember: function() {
            this.$emit('close-view');
            this.resetInfo();
        },
        storeMember: function() {

            this.hasSubmit = true;
            if (!this.name || !this.birthday || !this.height || !this.weight) { return }

            let info = {
                id: new Date().getTime(),
                name: this.name,
                birthday: this.birthday,
                gender: this.gender,
                height: this.height,
                weight: this.weight,
                career: this.career,
                sportFrequancy: this.sportFrequancy,
                sportGoal: this.sportGoal
            };
            this.$emit('store-member', info);
            console.log('info: ' + info.id);
            this.resetInfo();
            console.log('reset!!');
        },
        resetInfo: function() {
            this.hasSubmit = false;
            this.name = '';
            this.birthday = '';
            this.gender = '男';
            this.height = '';
            this.weight = '';
            this.career = '';
            this.sportFrequancy = '';
            this.sportGoal = '';
        }
    }
}
</script>

<style>
.my-btn-close {
    height: 60px;
    width: 60px;
    background-image: url('~@/assets/icon_close.png');
    background-repeat: no-repeat;
    background-position: center;
    background-size: 30px;
}
.dimming-view {
    background-color: rgba(0, 0, 0, 0.8);
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
}
</style>